body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  
}
html {
  background-color: #e3e3e3;
}
.panel-container {
  position: fixed;
  top: 50%;
  -webkit-flex-direction: row;
          flex-direction: row;
  left: 50%;
  /* box-shadow: 10px 10px 79px 0px rgba(0,0,0,0.75); */
  /* border: 1px solid white; */
  display: -webkit-flex;
  display: flex;
  width: 730px;
  height: 430px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.panel {
  width: 100%;
  color: white;
  line-height: 26.5em;
  /* border: 1px solid white; */
}
.panel:hover {
  /* box-shadow: 0px 10px 5px 0px rgba(0,0,0,0.75); */
  /* box-shadow: 0px 12px 100px 0px rgba(0,0,0,0.75); */
  color: white !important;
  border: 1px solid #261730;
  box-shadow: inset 0px 12px 100px 0px rgba(0,0,0,0.75);
  /* transform: rotate(10deg); */
}
.panel:hover p {
  visibility: visible;
}
.panel p {
  cursor: pointer;
  text-transform: capitalize;
  visibility: hidden;
}

@media only screen and (max-width: 768px) {
  .panel-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 10px;
    width: 200px;
    -webkit-transform: translate(-50%, -1500%);
            transform: translate(-50%, -1500%);
  }
  .panel {
    line-height: 10px;
  }
}
